body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.fullpage {
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background-color: whitesmoke;
  overflow: hidden;
  color: var(--base);
}

.fullpage-first-element {
  margin-top: auto;
  padding-top: 20px;
}



.next-page {
    font-size: 10rem;
    cursor: pointer;
    margin-top: auto;
    -webkit-animation: up-mover 0.7s infinite alternate;
    animation: up-mover 0.7s infinite alternate;
}

.next-page.up {
    -webkit-animation: down-mover 0.7s infinite alternate;
    animation: down-mover 0.7s infinite alternate;
}

@-webkit-keyframes up-mover {
    0% {
        -webkit-transform: translateY(-40px);
                transform: translateY(-40px);
    }
    100% {
        -webkit-transform: translateY(-70px);
                transform: translateY(-70px);
    }
}
@keyframes up-mover {
    0% {
        -webkit-transform: translateY(-40px);
                transform: translateY(-40px);
    }
    100% {
        -webkit-transform: translateY(-70px);
                transform: translateY(-70px);
    }
}

@-webkit-keyframes down-mover {
    0% {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
    100% {
        -webkit-transform: translateY(50px);
                transform: translateY(50px);
    }
}
@keyframes down-mover {
    0% {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
    100% {
        -webkit-transform: translateY(50px);
                transform: translateY(50px);
    }
}

@media only screen and (max-width: 768px) {
    .next-page {
        font-size: 8rem;
    }
}

.title-icons-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.icon {
  margin-left: 10px;
  margin-right: 10px;
  font-size: 10rem;
}

.title {
  padding-top: 150px;
  text-align: center;
}

.sub-title {
  text-align: center;
}

@media only screen and (max-width: 768px) {
  .title {
    padding-top: 50px;
    text-align: center;
  }
}

.first {
  height: 100vh;
  overflow: hidden;
}

.second {
  background-color: var(--base);
  color: whitesmoke;
}
.paragraphs {
  padding: 0 10px;
  text-align: center;
}
.card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 160px;
  height: 220px;
  margin: 10px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  color: whitesmoke;
  background-color: var(--base);
}
.card:hover {
  box-shadow: 5px 10px 8px #888888;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.skill-icons-wrapper {
  width: 160px;
  height: 140px;
  text-align: center;
  padding-top: 10px;
  font-size: 9rem;
}
.skill-title-wrapper {
  padding: 10px;
  height: 80px;
  width: 160px;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: flex-end;
}
.black {
  color: black;
}



.cards-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.navbar {
  z-index: 1;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100px;
  background-color: rgba(245, 245, 245, 0.7);
  color: var(--base);
  font-size: 1.5rem;
}

.navbar ul {
  list-style: none;
}

.navbar input {
  width: 100px;
}

.nav-links-wrapper {
  display: flex;
}

.nav-links-wrapper li {
  padding: 15px 40px;
}

.nav-links-wrapper li:hover {
  cursor: pointer;
  text-decoration: underline;
}

.nav-color-input {
  margin-left: 20px;
}

@media only screen and (max-width: 768px) {
  .navbar {
    font-size: 1rem;
    height: 30px;
  }

  .nav-links-wrapper li {
    padding: 10px 10px;
  }
}


html,
body {
  height: 100%;
  margin: 0;
  font-family: "Lato", sans-serif;
  scroll-behavior: smooth;
}

h3 {
  font-size: 2.5rem;
}

h4 {
  margin: 0;
  font-size: 20px;
}

p {
  font-size: 1.5rem;
}

#snow-target {
  position: relative;
  overflow: hidden;
  _zoom: 1; /* IE 6 display fix */
}

