.title-icons-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.icon {
  margin-left: 10px;
  margin-right: 10px;
  font-size: 10rem;
}

.title {
  padding-top: 150px;
  text-align: center;
}

.sub-title {
  text-align: center;
}

@media only screen and (max-width: 768px) {
  .title {
    padding-top: 50px;
    text-align: center;
  }
}

.first {
  height: 100vh;
  overflow: hidden;
}
